
  import { defineComponent, ref, watch } from 'vue';

  const MOMCompare = defineComponent({
    props: {
      title: {
        type: String,
        required: true,
      },
      number: {
        type: Number,
        required: true,
      },
      compare: {
        type: Number,
        required: true,
      },
    },
    setup(props) {
      const compareValue = ref('up');

      watch(() => props.compare, () => {
        if (props.compare && props.compare.toString().includes('-')) {
          compareValue.value = 'down';
        }
      }, {
        immediate: true,
      });

      return {
        compareValue,
      };
    },
  });

  export default MOMCompare;
